

@import '@/assets/scss/variables.scss';

.github-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  margin-left: 10px;

  &__avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    @include apply-shadow;
    margin-right: 10px;
    transition: scale 0.5s;
  }

  &:hover {
    .github-link__avatar {
      scale: 1.2;
    }
  }

  &__name {
    font-size: 1rem;
    font-family: 'Poppins', sans-serif;
  }

  &:nth-of-type(2) {
    margin-left: 30px;
  }
}

