

@import '@/assets/scss/variables.scss';

.not-found {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  &__heading {
    font-size: 5rem;
    user-select: none;
    font-family: 'Pacifico', cursive;
    margin: 0;
    margin-bottom: 10px;
  }

  &__desc {
    user-select: none;
    font-weight: 300;
    font-size: 2rem;
    margin: 0;
  }
}

