@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap";
@import "https://fonts.googleapis.com/css2?family=Pacifico&display=swap";
@font-face {
  font-family: "Nunito";
  src: url("@/assets/fonts/Nunito-Medium.ttf");
  font-weight: normal;
}
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
}

.btn {
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  border: none;
  padding: 0;
}

.a {
  text-decoration: none;
  color: inherit;
}

.a:visited {
  color: inherit;
}

.a:hover {
  color: inherit;
}