
@import './common.scss';

.product-card {
  width: 250px;
  display: flex;
  flex-direction: column;
  height: fit-content;
  align-items: center;
  justify-content: space-between;

  &__head {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 20px;
    max-width: 100%;
  }

  &__body {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__cart-btn {
    width: 60px;
    height: 60px;
    border-radius: 50%;
  }
}
