
@import '@/assets/scss/variables.scss';

.my-select {
  padding: 0.3em 0.6rem;
  font: {
    family: 'Poppins', sans-serif;
    size: 1rem;
    weight: 500;
  }
  border: none {
    radius: 10px;
  }
  outline: 2px solid $secondary;
  transition: all 0.2s;
  cursor: pointer;

  &:focus-visible {
    transform: scale(1.05);
    outline-color: $primary;
  }
}
