
.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;

  &__item {
    margin: 20px;
  }

  &_not-found {
    min-height: 60%;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    font-weight: 700;
  }
}

.loading {
  width: 100%;
  height: 100px;
  display: block;
}
