
@import '@/assets/scss/variables.scss';

.price__wrapper {
  flex-basis: 20%;
  font: {
    size: 1.3rem;
    weight: 600;
  }

  .product__price-info {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    position: sticky;
    top: 1rem;
    padding: 1rem;
    background-color: white;
    border-radius: 10px;
    @include apply-shadow;

    .product {
      &__prices {
        display: flex;
        align-items: flex-end;
        gap: 1rem;

        .crossed {
          font: {
            size: 0.8em;
            weight: 400;
          }
          color: $danger;
          text-decoration: line-through;
        }
      }

      &__stock {
        font-size: 1rem;
        font-weight: 400;
        color: $secondary;
      }

      &__buttons {
        display: flex;
        justify-content: center;
        gap: 1rem;

        .button__cart {
          min-width: 10.1em;
        }
      }
    }
  }
}
